.app {
  text-align: center;
  font-family: sans-serif;
}

.app-title {
  text-align: center;
  margin-bottom: 60px;
  margin-top: 0;
  text-shadow: 0 2.8px 4.2px green, 0 5.7px 6px blue, 0 10px 11px red,
    0 14.3px 15.9px purple, 0 16.8px 12px orange, 0 19px 17px teal;
}

@media (prefers-reduced-motion: no-preference) {
  .app-title {
    animation: app-title-spin 10s linear infinite;
  }
}

@keyframes app-title-spin {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(360deg);
  }
}

.app-container {
  box-sizing: border-box;
  width: fit-content;
  max-width: 500px;
  min-height: 200px;
  margin: 0 auto;
  padding: 40px 60px;

  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.6);
  background-color: #15172b;
  border-radius: 20px;
}
